import React from 'react';

export default class Questions extends React.Component {
  render() {
    const { questions } = this.props;
    return (
      <ol>
        {questions.map(fullQuestion => {
          if (React.isValidElement(fullQuestion)) {
            return fullQuestion;
          }
          const [line, question] = fullQuestion.split(':');
          return (<li><strong>{!!question ? `${line}: ` : ''}</strong>{!!question ? question.trim() : fullQuestion}</li>);
        })}
      </ol>
    );
  }
}